<template>
  <div class="available-voucher">
    <div class="available-voucher-list-wrap">
      <div class="ul-box">
        <coupon-list :list="list" :memberCouponId="memberCouponId" @fnSelectVoucher="fnSelectVoucher" @received="fnConfirm"></coupon-list>
      </div>
      <!-- <available-voucher-list :list="list" :memberCouponId="memberCouponId" @fnSelectVoucher="fnSelectVoucher" /> -->
      <!-- <div class="no-more-data">已无更多</div> -->
      <div class="btn-wrap">
        <div class="btn" @click="fnConfirm">确定</div>
      </div>
    </div>
    <no-data v-if="!list.length"></no-data>
  </div>
</template>

<script>
import couponList from '../create/order-coupon/coupon-list-selected.vue' // 可用

// import availableVoucherList from './components/available-voucher-list'
import noData from './components/no-data'
import {
  getStoreCoupon,
  chooseTailCoupons,
  getOrderCoupon,
  getTailCoupons,
  choiceStoreCoupon,
  choiceOrderCoupon,
} from '@/http/service/goods'
export default {
  components: {
    couponList,
    noData,
  },
  data() {
    return {
      type: '', // 1 店铺券  2 平台券
      productId: '',
      sku: '',
      orderSn: '',
      list: [],
      memberCouponId: '',
      showVoucherTypeFlag: false,
      plantType: '',
      activityId: '',
      itemType: '',
      couponValue: '',
      couponFrom: '', // 优惠券来源
      scope: '',
    }
  },
  created() {
    console.log('进来没')
    this.type = this.$route.query.type
    if (this.$route.query.from === 'tailOrder') {
      // 尾款支付
      this.orderSn = this.$route.query.orderSn
      this.getTailCoupons()
      return
    }
    console.log('我在这')
    console.log(this.type)
    if (this.type === '1') {
      this.productId = this.$route.query.productId
      this.sku = this.$route.query.sku
      this.getStoreCoupon()
    } else {
      this.getOrderCoupon()
    }
  },
  methods: {
    /**
     * @description 获取店铺优惠券
     */
    getStoreCoupon() {
      getStoreCoupon({
        productId: this.productId,
      }).then(res => {
        if (res.success) {
          res.data.forEach((item, index) => {
            item.voucherType = 1
          })
          this.list = [...res.data]
          this.list.forEach(item => {
            if (item.isForUse === 1) {
              // 默认选中
              this.memberCouponId = item.couponCode
              this.couponValue = item.couponValue
            }
          })
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 获取平台优惠券
     */
    getOrderCoupon() {
      getOrderCoupon().then(res => {
        if (res.success) {
          for (let i = 0; i < res.data.length; i++) {
            // if (!res.data[i].canUse) {
            //   res.data.splice(i, 1)
            //   i--
            // }
          }
          res.data.forEach((item, index) => {
            item.voucherType = 2
          })
          this.list = [...res.data]
          this.list.forEach(item => {
            if (item.isForUse === 1) {
              // 默认选中
              this.memberCouponId = item.couponCode
              this.scope = item.scope;
            }
          })
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 定金+尾款优惠券
     */
    getTailCoupons() {
      getTailCoupons({
        orderSn: this.orderSn,
      }).then(res => {
        if (res.success) {
          res.data.forEach((item, index) => {
            item.voucherType = 2
            // 将 memberCouponId 赋值给 couponCode
            if (item.memberCouponId) {
              item.couponCode = item.memberCouponId + ''
            }
          })
          this.list = [...res.data]
          this.list.forEach(item => {
            if (item.isForUse === 1) {
              // 默认选中
              this.memberCouponId = item.couponCode
              // this.plantType = item.type
              // this.activityId = item.activityId
              this.couponValue = item.couponValue
              this.scope = item.scope;
            }
          })
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 选择优惠券
     */
    fnSelectVoucher(data) {
      if (this.$route.query.from === 'tailOrder') {
        // 尾款支付
        if (this.memberCouponId === data.couponCode) {
          // 取消选中
          // this.activityId = ''
          // this.itemType = ''
          this.couponValue = data.couponValue
        } else {
          this.couponValue = data.couponValue
          // this.activityId = data.activityId
          // this.itemType = data.type
        }
        this.memberCouponId = this.memberCouponId === data.couponCode ? '' : data.couponCode
        this.scope = data.scope;
        return
      }

      // 暂时用下
      this.couponFrom = data.type

      if (this.type === '1') {
        // 店铺券
        if (this.memberCouponId === data.couponCode) {
          // this.plantType = ''
          // this.activityId = ''
          this.couponValue = data.couponValue
        } else {
          this.couponValue = data.couponValue
          // this.plantType = data.type
          // this.activityId = data.activityId
        }
      }
      this.memberCouponId = this.memberCouponId === data.couponCode ? '' : data.couponCode
    },
    fnConfirm() {
      if (this.$route.query.from === 'tailOrder') {
        // 尾款支付选择优惠券
        chooseTailCoupons({
          'orderSn': this.orderSn,
          'couponCode': this.memberCouponId,
          'scope': this.scope,
          // 'couponValue': this.couponValue
        }).then(res => {
          if (res.success) {
            this.$router.go(-1)
          } else {
            this.$toast(res.message)
          }
        })
        return
      }

      if (this.type === '1') {
        this.choiceStoreCoupon()
      } else {
        this.choiceOrderCoupon()
      }
    },
    /**
     * @description 选择店铺优惠券
     */
    choiceStoreCoupon() {
      choiceStoreCoupon({
        couponCode: this.memberCouponId,
        sku: this.sku,
        // couponValue: this.couponValue | null,
      }).then(res => {
        if (res.success) {
          this.$router.go(-1)
        } else {
          this.$toast(res.message)
        }
      })
    },
    /**
     * @description 选择平台优惠券
     */
    choiceOrderCoupon() {
      choiceOrderCoupon({
        'couponCode': this.memberCouponId,
        // 'couponValue': this.couponValue || null,
      }).then(res => {
        if (res.success) {
          this.$router.go(-1)
        } else {
          this.$toast(res.message)
        }
      })
    },
    fnShowVoucherType() {
      this.showVoucherTypeFlag = !this.showVoucherTypeFlag
    },
  },
}
</script>
